import React from 'react';
import ListItem from './ListItem';

const VacancyList = ({ list/*, filter */}) => {
      let rows

      if (list.length !== 0 ) {
          rows = list.map((item, id) => {
                      return <ListItem item={item} key={id} />
                   });
      }
      else {
        rows = <tr className='undefined'><td className='first_row'>  !</td><td className='second_row'></td></tr>
      }
        return (
          <table className='list'>
            <caption> </caption>
            <thead>
              <tr>
                <th className='first_row'></th>
                <th className='second_row'></th>
              </tr>
            </thead>
            <tbody>{rows}</tbody>
          </table>
        );
      }

export default VacancyList


